










import Vue from "vue";
import SettingsElement from "@/components/SettingsElement.vue";

interface DataTypeDescriptor {
	settingsText: string;
}

export default Vue.extend({
	name: 'SettingsWindow',
	components: {
		SettingsElement,
	},
	data(): DataTypeDescriptor {
		return {
			settingsText: this.$store.state.isMac ? 'Preferences' : 'Settings'
		};
	},
})
